.image.series-icon::before {
	content: '';
	background-image: url('/wp-content/themes/rueckblende/build/assets/img/archive/icon_series.svg');
	background-size: contain;
	background-repeat: no-repeat;
	height: 25px;
	width: 30px;
	position: absolute;
	bottom: 5px;
	left: 5px; }

.pagination {
	// width: 33.33vw
	width: 9rem;
	margin: 0 auto 4rem auto;
