		font-weight: bold;
		text-align: center;
		font-size: 1.3rem;
		margin-top: -0.5rem; }
	.arrow {
		&:hover {
			opacity: .6; }
		&.prev {
 } } }			// margin-right: 4rem

// search results and ajax archive pages

.is-loading {
	background-size: 100px 100px !important;
	width: 100% !important;
	background-repeat: no-repeat !important;
	background-position: center 50px !important;
	background-image: url(/wp-content/themes/rueckblende/puff.svg) !important;
	min-height: 200px !important; }

strong.pad {
	display: block;
	margin-bottom: 1rem; }

#results, .fetch, .section.winners {
	transition: height 200ms;
	background: white;
	min-height: 200px;
	.column {
		transform: scale(0);
		opacity: 0;
		transition: 200ms all;
		overflow: hidden;
		// max-height: 22rem
		margin-bottom: 1rem;
		@include mobile {
			padding-bottom: 1em;
			margin-bottom: 1em;
			border-bottom: 1px solid #ececec; }
		&:hover .name {
			color: $red; } }

	a .name {
		color: #4a4a4a; }
	.image {
		@include mobile {
			height: 58vw; }
		@include tablet {
			height: 125px; }
		@include desktop {
			height: 148px; }
		@include widescreen {
			height: 180px; }
		@include fullhd {
			height: 212px; }
		width: 100%;
		// background-color: #f9f9f9
		background-repeat: no-repeat;
		background-position: bottom;
		background-size: contain;
		margin-bottom: 0.8em;
 }		// background-color: #f9f9f9
	.name {
		// padding: 0 0 10px
		font-weight: bold;
		font-size: 1.1rem;
		margin-bottom: 0.4rem;
		a:hover {
			color: $red; }
		.year {
			font-weight: 200;
			font-size: .8em; } }
	.caption, .series {
		font-size: 1rem;
		color: #878787; }
	.caption {
		height: 5.8rem;
		overflow: hidden; }
	.series {
		font-weight: bold;
		// margin-top: -0.4rem
		// margin-bottom: 0.5rem
		margin-bottom: 3px; }
	.error {
		height: 300px;
		// background: pink
		display: flex;
		justify-content: center;
		align-items: center;
		// color: #F00
		font-size: 2rem; }
	.loaded {
		transform: scale(1);
		opacity: 1;
		transition: 200ms all; } }

