$red: #AB002C;
$red2: #871D33;
$lg: #EBEBEB;

body {
  font-size: 18px; }

a {
  color: $red; }

.owl-item {
  display: flex;
  justify-content: center; }

.owl-nav {
  .owl-prev, .owl-next {
    position: absolute;
    top: calc(50% - 26px);
    transition: all 150ms;
    z-index: 88;
    &:hover {
      transform: scale(1.3); }
    &:before {
      height: 26px;
      width: 26px;
      content: ' ';
      background-size: contain;
      background-repeat: no-repeat;
      display: block;
      background-position: center center; } }
  .owl-prev {
    left: -40px; }
  .owl-next {
    right: -40px; }
  .owl-prev:before {
    background-image: url(/wp-content/themes/rueckblende/gallery-left.svg); }
  .owl-next:before {
    background-image: url(/wp-content/themes/rueckblende/gallery-right.svg); } }
.arrows-inside {
  .owl-prev {
    left: 40px; }
  .owl-next {
    right: 40px; }
  .owl-prev:before {
    background-image: url(/wp-content/themes/rueckblende/arrow_red_left.svg); }
  .owl-next:before {
    background-image: url(/wp-content/themes/rueckblende/arrow_red_right.svg); } }

@include desktop {
  .home .owl-carousel .owl-item img {
    height: 600px;
    width: auto; } }

#debug, xmp {
  font-size: 12px;
  line-height: 1em;
  color: white;
  background: black;
  padding: 10px; }

textarea, input, button, *:focus {
  outline: none !important;
  box-shadow: none !important; }

#menu {
  // display: none
  transform: translateX(-100vw);
  transition: all 500ms;
  width: 100vw;
  height: 100vh;
  background: rgba(255,255,255,0);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1111;
  &.is-active {
    // display: block
    transform: translateX(0);
    background: rgba(0,0,0,0.9); }
  .menu-wrapper {
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    font-size: 46px;
    font-weight: bold;
    line-height: 72px;
    text-align: right;
    @include mobile {
      font-size: 35px;
      line-height: 70px; }
    a {
      color: white; } }
  .badge, .close {
    position: fixed;
    top: 50px; }
  .badge {
    left: 50px;
    @include mobile {
      left: 20px;
      top: auto;
      bottom: 10px;
      width: 70px; } }

  .close {
    right: 50px;
    cursor: pointer;
    @include mobile {
      top: 25px;
      left: 25px;
      right: auto; }
    &:hover {
      opacity: .5; } }
  hr {
    height: 0;
    border-bottom: 1px white solid;
    margin-left: 0;
    margin-right: 0; }
  .small-links {
    font-size: 16px !important;
    line-height: 38px !important; } }

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center; }
.flex-space-between {
  display: flex;
  justify-content: space-between;
  align-items: center; }

