#header {
  height: 70px;
  background: #FAFAFA;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.50);
  overflow: hidden;
  .flex-spacer {
    width: 100px; }
  .site-title {
    margin: 10px 0 0 0;
    padding: 0;
    width: 190px;
    height: auto; }
  .navbar-burger {
    padding: 0;
    margin: 0;
    border: 0 !important;
    margin-left: 0;
    display: inline;
    width: 100px;
    background: transparent !important;
    &:hover {
      background: transparent !important; }
    span {
      height: 0px;
      width: 25px;
      border: 2px $red solid;
      border-radius: 3px; }
    span:nth-child(1) {
      top: calc(50% - 8px); }
    span:nth-child(2) {
      top: calc(50% - 1px); }
    span:nth-child(3) {
      top: calc(50% + 6px); }
    &.is-active span:nth-child(1) {
      transform: translateY(9px) rotate(45deg); } } }

